<script>
import DataService from '../../services/DataService';
export default {
    data: function () {
            return {  
                isLoading: false,
                file:null,
                filepath:null,   
                excelFileName:null,
                reqfrom:null,   
            }
        },   
   created:function(){
    this.reqfrom=this.$route.params.reqfrom;
   },
   methods:{
    
    BackToList:function(){
        this.$router.push({name:'ContactListing'}); 
    },
    setFile:function(e){
    this.file=e.target.files[0];
    this.excelFileName=e.target.files[0].name;
    },
    resetFile:function(){
        var vm = this;
        vm.file=null;
        vm.excelFileName=null;
    },
    UploadContactFile:function() {
        var vm=this;
        if(vm.file!=null)
        {
            this.isLoading=true;
            const formdata=new FormData();
            formdata.append("file",vm.file);
            DataService.ImportContactsPost(formdata).then(function(response) { 
            var data=response.data;     
                if (data.Status == 0) {
                    vm.filepath = data.Msg;
                    setTimeout(function () { vm.loadExcelHeader(data.Msg); }, 1000);
                    setTimeout(function () { vm.loadTableHeader(); }, 2000);
                    vm.isLoading=false;
                }
                else {
                    vm.isLoading=false;
                  
                    vm.ShowAlert(data.Msg, "warning", true, vm.$t("Alert"));
                }
            });
        }
        else{
            vm.ShowAlert(vm.$t("PleaseUploadExcelFile"), "warning", true, vm.$t("Alert"));
        }
        
        
    },
    loadExcelHeader:function(filepath){
        var vm=this;
        vm.isLoading=true;
        var param=`filepath=${filepath}&excelFileName=${vm.excelFileName}`;
        DataService.GetContactExcelColumn(param)
        .then(function(response) {
           var data=response.data;
            vm.isLoading=false;
            if (data.length != undefined) {
                   var select = $("#ddlExcelColumn");
                    select.empty();
                    $.each(data, function (index, itemData) {
                        $('#hdnBatchId').val(itemData.batch_id);
                        $("#ddlExcelColumn").append("<option value='" + itemData.column_Name + "'Data_Type='" + itemData.data_Type + "' >" + itemData.column_Name + "</option>");
                    });
                }
        })
    },
    loadTableHeader:function(){
        var vm=this;
        vm.isLoading=true;
      
        DataService.GetContactTableColumn()
        .then(function(response) {
            var data=response.data;
            vm.isLoading=false;
            if (data.length != undefined) {
                   var select = $("#ddlTableColumn");
                    select.empty();
                    $.each(data, function (index, itemData) {
                        $("#ddlTableColumn").append("<option value='" + itemData.column_Name + "'Data_Type='" + itemData.data_Type + 
                        "'Is_Required='" + itemData.is_Required + "'Picklist_Options='" + itemData.picklist_Options +
                         "'Custom_Field_Id='" + itemData.custom_Field_Id + "' >" + itemData.column_Name.replace(/_/g,' ') + "</option>");
                    });
                    vm.createMultiRows(data.length);
                }
        })
    },
    createMultiRows:function(totalRecords) {
        try {
            var vm=this;
            $('uploadfile').addClass("disabled");
            $('#savebtn').removeClass("d-none");
            var rownumber = 1002;
            vm.isLoading=true;
            var table = $("table#tblBulkContact tr#trdata");
            var Exceloptions = "";
            var Tableoptions = "";
            $(".norecord").addClass("d-none");
            for (var i = 0; i < totalRecords; i++) {
                
                $(table).clone().attr({ 'crtrwid': rownumber }).removeClass("d-none").addClass("visible")
                    .find("input,a,select,div").each(function () {
                        $(this).attr({
                            'id': function (_, id) { return id + "" + rownumber; },
                            'name': function (_, name) { return name + "" + rownumber; },
                            'value': function (_, value) { return value; }
                        });
                        $(this).removeClass("d-none");
                    }).end().appendTo("table#tblBulkContact");
                Exceloptions = $('#ddlExcelColumn').children('option').clone();
                Tableoptions = $('#ddlTableColumn').children('option').clone();
                $("table#tblBulkContact tr.visible td select[id='ddlTableColumn" + rownumber + "']").remove();
                $("table#tblBulkContact tr.visible td select[name='TableColumns" + rownumber + "']").attr('id', 'ddlTableColumn' + rownumber);
                $("table#tblBulkContact tr.visible td select[name='TableColumns" + rownumber + "']").attr('onclick', 'CheckDuplicateTableRecord(ddlTableColumn' + rownumber + ')');
                $("table#tblBulkContact tr.visible td select[name='TableColumns" + rownumber + "']").append(Tableoptions);
                $("table#tblBulkContact tr.visible td select[id='ddlExcelColumn" + rownumber + "']").remove();
                $("table#tblBulkContact tr.visible td select[name='ExcelColumns" + rownumber + "']").attr('id', 'ddlExcelColumn' + rownumber);
                $("table#tblBulkContact tr.visible td select[name='ExcelColumns" + rownumber + "']").attr('onclick', 'CheckDuplicateExcelRecord(ddlExcelColumn' + rownumber + ')');
                $("table#tblBulkContact tr.visible td select[name='ExcelColumns" + rownumber + "']").append(Exceloptions);
           
                if (Exceloptions.length > i) {
                    var tc_datatype = $("#ddlTableColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('data_type');
                    var ex_datatype = $("#ddlExcelColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('data_type');
                    var tc_Value = $("#ddlTableColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('value');
                    var ex_Value = $("#ddlExcelColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('value');
                 
                    if (tc_datatype != undefined && ex_datatype != undefined)
                    {
                      
                        if (tc_Value.toLowerCase() == ex_Value.toLowerCase())
                        {
                            $("#ddlTableColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('selected', 'selected');
                            $("#ddlExcelColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('selected', 'selected');
                        }
                       
                    }
                }
                rownumber++;
            }
            $("table#tblBulkContact tr.visible[crtrwid ='" + 1001 + "']").removeClass("visible").addClass("d-none");
            vm.isLoading=false;
        } catch (ex) {
            this.isLoading=false;
         
        }
    },
     CheckDuplicateTableRecord:function(ctrlid) {
        //
        var objarr = [];
        var flag = true;
        $("table#tblBulkContact tr#trdata").each(function () {
        
            var ddlval = $(this).find("td").eq(0).find("select option:selected").text();
            objarr.push(ddlval);
        });
        var controlid;
        var dupid2;
        var dupid;
        var indexno = 1002;
        flag = false;
        var i; var j;
        for (i = 1; i < objarr.length; i++) {
            controlid = '#ddlTableColumn' + indexno;
         
            for (j = i + 1; j < objarr.length; j++) {
          
                if (objarr[i] == objarr[j] && objarr[i] != "Select Column") {
                    flag = true;
                    //$(controlid).addClass('border border-danger');
                    dupid = controlid;
                    dupid2 = '#ddlTableColumn' + (1002 + (j - 1));
                    break;
                }
            }
            if (flag) {
                break;
            }
            $(controlid).removeClass('border border-danger');
            $(savebtn).removeClass("disabled");
            indexno++;
        }
        if (flag) {
            $(dupid).addClass('border border-danger');
            $(dupid2).addClass('border border-danger');
            $(savebtn).addClass("disabled");
        }
        flag = false;
        $("table#tblBulkContact tr#trdata").each(function () {
           if ($(this).find("td").eq(1).find("select").hasClass("border-danger")) {
                $(savebtn).addClass("disabled");
            }
        }); 
    },
     CheckDuplicateExcelRecord:function(ctrlid) {
        //
        var objarr = [];
        var flag = true;
        $("table#tblBulkContact tr#trdata").each(function () {
        
            var ddlval = $(this).find("td").eq(1).find("select option:selected").text();
            objarr.push(ddlval);
        }); 
        var controlid;
        var dupid2;
        var dupid;
        var indexno = 1002;
        var flag = false;
        var i; var j;
        for (i = 1; i < objarr.length; i++) {
            controlid = '#ddlExcelColumn' + indexno;
        
            for (j = i + 1; j < objarr.length; j++) {
           
                if (objarr[i] == objarr[j] && objarr[i] != "Select Column") {
                    flag = true;
                    //$(controlid).addClass('border border-danger');
                    dupid = controlid;
                    dupid2 = '#ddlExcelColumn' + (1002 + (j - 1));
                    break;
                }
            }
            if (flag) {
                break;
            }
            $(controlid).removeClass('border border-danger');
            $(savebtn).removeClass("disabled");
            indexno++;
        }
        if (flag) {
            $(dupid).addClass('border border-danger');
            $(dupid2).addClass('border border-danger');
            $(savebtn).addClass("disabled");
        }
        flag = false;
        $("table#tblBulkContact tr#trdata").each(function () {
           if ($(this).find("td").eq(0).find("select").hasClass("border-danger")) {
                $(savebtn).addClass("disabled");
            }
        });
    },
    SaveContactFile() {
        var vm=this;
        var xml = "<contactentries>\n";
        var isError = false;
        var rowid = 0;
        var saveFlag = false;
        $("table#tblBulkContact tr.visible").each(function () {         
            rowid = $(this).attr("crtrwid");
            $("table#tblBulkContact tr.visible[crtrwid ='" + rowid + "']").removeClass('alert-warning')         
            if (!$(this).hasClass('alert-success')) {            
                if ($('#ddlTableColumn' + rowid).val() == "") {
                    $('#ddlTableColumn' + rowid).addClass('is-invalid');
                    isError = true;
                } else {
                    $('#ddlTableColumn' + rowid).removeClass('is-invalid');
                }
                if ($('#ddlExcelColumn' + rowid).val() == "") {
                    $('#ddlExcelColumn' + rowid).addClass('is-invalid');
                    isError = true;
                } else {
                    $('#ddlExcelColumn' + rowid).removeClass('is-invalid');
                }
                if (isError) {
                    $('#lblError').parent().removeClass('d-none');
                    $('#lblError').html(vm.$t('PleaseCorrectHighlightedErrors'));
                    $(document).scrollTop(0);
                 
                } else {
                    $('#lblError').html('');
                    $('#lblError').parent().addClass('d-none');
                   
                    rowid = $(this).attr("crtrwid");
                    var original_column_name = $(this).find('#ddlTableColumn' + rowid).val();
                    var column_name = $(this).find('#ddlTableColumn' + rowid).val();
                    var data_type = $(this).find('#ddlTableColumn' + rowid).find(":selected").attr('data_type');
                    var custom_field_id = $(this).find('#ddlTableColumn' + rowid).find(":selected").attr('Custom_Field_Id');
                    var look_up = $(this).find('#ddlTableColumn' + rowid).find(":selected").attr('Picklist_Options');
                    var excel_column_name = $(this).find('#ddlExcelColumn' + rowid).val();
                    var excel_data_type = $('#ddlExcelColumn' + rowid).find(":selected").attr('data_type');
                    if (look_up == null) {
                        look_up = '';
                    }
                    if (column_name != undefined) {
                        saveFlag = true;
                        xml += "\n<contact>" +
                             "\n\t<batch_id>" + $('#hdnBatchId').val() + "</batch_id>" +
                             "\n\t<row_id>" + rowid + "</row_id>" +
                            "\n\t<original_column_name>" + original_column_name + '_' + look_up + "</original_column_name>" +
                            "\n\t<column_name>" + column_name + "</column_name>" +
                            "\n\t<data_type>" + data_type.toLowerCase() + "</data_type>" +
                            "\n\t<custom_field_id>" + custom_field_id + "</custom_field_id>" +
                            "\n\t<excel_column_name>" + excel_column_name.trim().replace(/[\r\n ]+/gm, '_').replace(/[^a-zA-Z0-9_ \s]/gi, '').replace(/[-\s]/g, '_') + "</excel_column_name>" +
                            "\n\t<excel_data_type>" + excel_data_type.toLowerCase() + "</excel_data_type>" +
                            "\n</contact>\n";
                    }
                }
            }
        });
        xml += "\n</contactentries>\n";
        if (!isError && saveFlag) {
            console.log({ xmltrackentries: xml, batch_id: $('#hdnBatchId').val() });
            var postJSON = {
                xmltrackentries: xml,
                batch_id: $('#hdnBatchId').val(),
                callingSource:"Contacts"
            };
            var objectPostString = JSON.stringify(postJSON);
          DataService.BulkUploadContact(objectPostString)
          .then(function (response) {
                   vm.isLoading=false;
                   var data=response.data;
              if (data.length != undefined) {
                  var reqfrom = vm.reqfrom;
                  if (reqfrom == "ContactListing") {
                      vm.$router.push({
                          name: 'CRMContactImportDetails', params: {
                              subModuleCode: 'CRM_CONTACTS',
                              reqfrom: 'ImportContacts',
                              batchid:postJSON.batch_id
                          }
                      });
                  }
                  else {
                      vm.$router.push({
                          name: 'CRMContactImportDetails', params: {
                              subModuleCode: 'CRM_ACCOUNTS',
                              reqfrom: 'ImportAccount',
                              batchid:postJSON.batch_id
                          }
                      });
                  }

              }
            });
        }
    }
   }
}
</script>
<template>
  <div>
    <loader :is-visible="isLoading"></loader>

    <section id="main-content" class="main-content">
         
      
                    <div class="row">
                        <div class="col-md-12 padding-t_8">
                            <div class="theme-primary partition-full">
                                <span class="p-name text-white">{{$t('Import')}} {{$t('Contacts')}}</span>
                                <span class="p-actions float-right">
                                   <a href="javascript:;" id="btnbacktolist" @click="BackToList" :title="$t('BacktoList')" class="p-action-btn text-white float-right"><em  class="fa fa-angle-double-left pr-2"></em><br />{{$t('BacktoList')}}</a>
                                </span>
                            </div>
                        </div>
                    </div>
              
        
            
                    <div class="border p-3">
                        <form>
                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="form-group">
                                        <label>{{$t('Download')}} {{$t('SampleFile')}}:</label>
                                        <div class="d-flex align-items-center">
                                            <p><a href="/Content/files/ImportContactsSampleFile.xls" checkprivilege="no" class="text-dark" download>
                                                <img src="/Content/images/excel-icon.png" alt="" class="mr-2" />{{$t('Contact')}} <em  class="fa fa-download ml-2 text-dark"></em></a></p>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-xl-4 col-lg-4 col-md-6">
                                    <div class="form-group">
                                        <label>{{$t('UploadFile')}}:<span class="text-danger">*</span></label>
                                        <div class="custom-file">
                                           
                                            <div class="custom-file">
                                                <input type="file" class="downloadexcel" id="flUserFile" size="19" name="flUserFile" tabindex="-1" @change="setFile($event)" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                                <div class="bootstrap-filestyle input-group">
                                                    <input type="text" class="form-control disabled" v-model="excelFileName" disabled="">
                                                    <div class="input-group-append" style="cursor:pointer;">
                                                        <span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0">
                                                            <label title="Click here to select a file." for="flUserFile" class="mb-0 font-weight-normal ">Choose File</label>
                                                        </span>
                                                        <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                            <a href="javascript:;" class="round-icon-small btn-danger-light clsattclear" title="Clear" data-toggle-tooltip="tooltip" data-original-title="Clear">
                                                                <em  class="fa fa-undo" @click="resetFile"></em>
                                                            </a>
                                                        </span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-xl-5 col-lg-6 col-md-12 mt-lg-4 pl-lg-0 ">
                                    <a href="javascript:void(0);" id="uploadfile" :title="$t('ImportFromExcelFile')" @click="UploadContactFile"
                                    class="btn btn-success mr-2"><em class="fa fa-sign-in pr-2" aria-hidden="true"></em>{{$t('ImportFromExcelFile')}}
                                    </a>
                                    <a href="/CRM/ImportContacts/ContactListing" id="" :title="$t('Reset')" class="btn btn-danger"><em  class="fa fa-refresh mr-2 "></em>{{$t('Reset')}}</a>
                             
                                </div>
                                <div class="col-lg-12">
                                    <div class="py-2">
                                        <span class="text-danger"><small>Fields marked with an asterisk (*) are mandatory.</small></span>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                
        </section>
        <div class="clearfix"></div>
     
        <div class="col-lg-12">
            <div class="listing mt-2">
                <div class="table-responsive">
                    <table id="tblBulkContact" class="table table-bordered dt-responsive nowrap mt-0" style="width:100%;" aria-describedby="importcontact">
                        <thead class="thead-bg">
                            <tr>
                                 <th scope="col">Table Column</th>
                                 <th scope="col">Excel Column</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr><td colspan="2" class="text-center text-danger norecord no-record">{{$t('NoRecordfound')}}</td></tr>
                            <tr id="trdata" crtrwid="1001" class="d-none">
                                <td>
                                    <select class="form-control d-none" name="TableColumns">
                                        <option value="">{{$t('Select')}} {{$t('Column')}}</option>
                                    </select>
                                    <select class="form-control" id="ddlTableColumn" name="TableColumn"></select>
                                </td>
                                <td>
                                    <select class="form-control" name="ExcelColumns">
                                        <option value="">{{$t('Select')}} {{$t('Column')}}</option>
                                    </select>
                                    <select class="form-control" id="ddlExcelColumn" name="ExcelColumn"></select>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div class="clearfix"></div>
                <div class="col-lg-12 text-right mt-3">
                    <input type="hidden" id="hdnBatchId" value="Batch_id" />
                    <a href="javascript:void(0);" @click="SaveContactFile" id="savebtn" :title="$t('Save')" class="btn btn-success my-2 d-none"><em  class="fa fa-save pr-2"></em><span>{{$t('Save')}}</span></a>
                </div>
            </div>
        </div>
    </div>
</template>